  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(100%);
  box-shadow: 0 4px 8px #0000000a, 0 2px 4px #00000014, 0 1px 3px #00000029;
}

.focus-in-expand {
  animation: .8s cubic-bezier(.25, .46, .45, .94) both focus-in-expand;
}

@keyframes focus-in-expand {
  0% {
    letter-spacing: -.5em;
    filter: blur(12px);
    opacity: 0;
  }

  100% {
    filter: blur();
    opacity: 1;
  }
}

@keyframes slide-in-top {
  0% {
    opacity: 0;
    transform: translateY(-1000px);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

