.backdrop {
  visibility: visible;
  opacity: 1;
  background: #00000080;
  width: 100vw;
  height: 100vh;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.backdrop.is-hidden .modal {
  transform: translate(-50%, -50%)scale(.9);
}

.modal {
  background: #fff;
  border-radius: 4px;
  width: 100%;
  max-width: 450px;
  max-height: 609px;
  padding: 40px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 1px 3px #0000001f, 0 1px 1px #00000024, 0 2px 1px #0003;
}

